<template>
    <form action="" class="form" @submit="checkLogin">
      <div class="errorLogin disable">
        <p class="errorLogin__text" id="errorMsg">
          Your username and/or password are incorrect
        </p>
      </div>
      <div class="form__group">
        <label for="Username" class="form__label">Login with username</label>
        <input
          type="text"
          class="form__input"
          placeholder="Username"
          id="Username"
          required
          v-model="username"
        />
      </div>
  
      <div class="form__group">
        <div class="linking">
          <label for="Password" class="form__label">Password</label>
          <a href="#" class="linking__forgetPassword">Forgot password?</a>
        </div>
        <input
          type="password"
          class="form__input valid"
          placeholder="8+ Characters"
          id="Password"
          required
          @change="
            passwordTouched = true;
            checkPassword();
          "
          @keyup="checkPassword"
          v-model="password"
        />
        <p class="errorMassage disable" id="passwordError">
          Password must be 6 characters or more and have uppercase !
        </p>
      </div>
      <button
        class="form__btn"
        id="submit"
        :disabled="!isValid"
        :class="{ avalible: isValid, notAvalible: !isValid }"
      >
        Login
      </button>
    </form>
    <div class="linking">
      <p for="Password" class="form__label">
        Don't have an account?<a href="#" class="linking__signup"> Sign up</a>
      </p>
    </div>
</template>
  
<script>
  export default {
    name: "loginForm",
    data() {
      return {
        username: "",
        password: "",
        usernameValid: true,
        passwordValid: false,
        passwordTouched: false,
        accounts: [
          { username: "mohamed@instabug.com", password: "A12345678" },
          { username: "mohamed1@instabug.com", password: "A12345678" },
          { username: "mohamed2@instabug.com", password: "A12345678" },
          { username: "mohamed3@instabug.com", password: "A12345678" },
          { username: "mohamed4@instabug.com", password: "A12345678" },
          { username: "mohamed5@instabug.com", password: "A12345678" },
          { username: "mohamed6@instabug.com", password: "A12345678" },
          { username: "mohamed7@instabug.com", password: "A12345678" },
        ],
      };
    },
    methods: {
      checkUpperCase(str) {
        return /[A-Z]/.test(str) && /[0-9]/.test(str);
      },
      checkPassword() {
        const userName = this.username.split("@")[0];
        if (
          this.password.length >= 6 &&
          this.checkUpperCase(this.password) &&
          this.password !== userName
        ) {
          document.getElementById("Password").classList.remove("notValid");
          document.getElementById("Password").classList.add("valid");
          document.getElementById("passwordError").classList.add("disable");
          this.passwordValid = true;
        } else if (this.passwordTouched) {
          document.getElementById("Password").classList.remove("valid");
          document.getElementById("Password").classList.add("notValid");
          document.getElementById("passwordError").classList.remove("disable");
          this.passwordValid = false;
        }
      },
      checkLogin(e) {
        e.preventDefault();
        const status = this.accounts.filter(
          (record) =>
            record.username === this.username && record.password === this.password
        );
        if (status.length > 0) {
          localStorage.setItem("userEmail", this.username);
          this.$router.push("/welcome");
        } else {
          document.querySelector(".errorLogin").classList.remove("disable");
          this.username = "";
          this.password = "";
          this.passwordValid = false;
        }
      },
    },
    computed: {
      isValid() {
        return this.emailValid && this.passwordValid && this.username;
      },
    },
  };
  </script>
  <style lang="scss" scoped>
  a:focus {
    outline: none;
  }
  .linking {
    width: 100%;
    display: flex;
    justify-content: space-between;
