      border: 1px solid white;
      font-size: 1.5rem;
      &:focus,
      &:active {
        outline: none;
      }
      &:placeholder-shown,
      &::placeholder {
        color: white;
      }
    }
    &__label {
      font-size: 1.5rem;
      margin-bottom: 0.5rem;
