      padding: 1rem;
      border-radius: 4px;
      margin-bottom: 1rem;
      cursor: pointer;
    }
  }
  .errorLogin {
    border-radius: 4px;
    margin: 0.5rem 0;
    padding: 1rem 1.5rem;
    &__text {
      font-size: 1.5rem;
      color: #256D85;
    }
  }
  .errorMassage {
    color: red;
    font-size: 1.2rem;
    margin-top: 1rem;
  }
  .notAvalible {
    background-color: #256D85;
    cursor: default;
  }
  .avalible {
    background-color: #47B5FF;
    cursor: pointer;
  }
  .notValid,
  .notValid:focus,
  .notValid:active {
    border-color: red;
  }
  .valid:focus,
  .valid:active {
    border-color: #DFF6FF;
    box-shadow: inset 0 0 4px 0 #DFF6FF;
  }
  .disable {
    display: none;
  }
  </style>